$blue: #0b0bd4;

h1 {
    font-size: 2.25rem;
    font-weight: 700;
}

h2 {
    font-size: 1.5rem;
    font-weight: 700;
}

a {
    text-decoration: none;
    color: $blue;
    cursor: pointer;
}

a:hover {
    text-decoration: underline;
}

.small {
    font-size: 80%;
    text-align: center;
}

.split-screen {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    height: 100%;
}

.left {
    flex: .3;
    height: 200px;
    background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0)), url(https://i.redd.it/spo5q1n66gg11.jpg);
    background-size: cover;
}

.left, .right {
    display: flex;
    justify-content: center;
    align-items: center;
}

.right {
    flex: .7;
}

.left .copy {
    color: white;
    text-align: center;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.left p {
    font-weight: 400;
}

.right .copy {
    color: black;
    text-align: center;
}

.right .copy p {
    margin: 1.5em 0;
    font-size: 0.875rem;
}

.right form {
    width: 328px;
}

form input:not([type='checkbox']) {
    display: block;
    width: 100%;
    box-sizing: border-box;
    border-radius: 8px;
    border: 1px solid #c4c4c4;
    padding: 1em;
    margin-bottom: 1.25rem;
    font-size: 0.875rem;
}

label {
    display: block;
    margin-bottom: 0.5rem;
    font-style: 0.75rem;
}

.input-container .password {
    position: relative;
}

.input-container .password i{
    position: absolute;
    top: 42px;
    right: 16px;
}

.input-container .cta {
    display: flex;
    flex-direction: row;
}

.checkbox-container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkbox-container {
    display: block;
    position: relative;
    padding-left: 2rem;
    cursor: pointer;
    user-select: none;
    margin: 0 0 1.25rem 0;
}

.checkbox-container input:checked- .checkmark {
    background-color: $blue;
    border: 1px solid $blue;
}

.checkmark {
    position: absolute;
    width: 24px;
    height: 24px;
    background: white;
    border: 1px solid #c4c4c4;
    border-radius: 2px;
    top: -4px;
    left: 0;
}

.checkmark::after {
    content: '';
    position: absolute;
    display: none;
}

.checkbox-container input:checked- .checkmark:after {
    display: block;
}

.checkbox-container .checkmark:after {
    border: solid white;
    border-width: 0 3px 3px 0;
    width: 5px;
    height: 10px;
    left: 8px;
    top: 4px;
    transform: rotate(45deg);
}

.signup-btn {
    display: block;
    width: 100%;
    background: $blue;
    color: white;
    font-weight: 700;
    border: none;
    padding: 1rem;
    border-radius: 8px;
    font-size: 0.75rem;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.signup-btn:hover {
    background: #2c4893;
    cursor: pointer;
}

@media screen and (min-width: 560px) {
    .split-screen {
        flex-direction: row;
        height: 100%;
    }
    .left, .right {
        flex: .5;
        display: flex;
        width: 50%;
        height: auto;
    }
}